body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro",Helvetica,"Helvetica Neue",Arial,sans-serif;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 0;
  background-color: #f5f5f5;
  color: #505050;
  text-rendering: optimizeLegibility;

  background: -webkit-gradient(linear, right top, left top, from(#b695a6), color-stop(#725d6c), color-stop(#3d3f4b), to(#5a616b));

  background: -webkit-linear-gradient(right, #b695a6, #725d6c, #3d3f4b, #5a616b);

  background: -o-linear-gradient(right, #b695a6, #725d6c, #3d3f4b, #5a616b);

  background: linear-gradient(-90deg, #b695a6, #725d6c, #3d3f4b, #5a616b);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
  animation: Gradient 15s ease infinite;
}

@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

.icons {
  border: 0.15rem solid #b3b3b3;
  border-radius: 50%;
  background: -webkit-gradient(linear, right top, left top, from(#b695a6), color-stop(#725d6c), color-stop(#3d3f4b), to(#5a616b));
  background: -webkit-linear-gradient(right, #b695a6, #725d6c, #3d3f4b, #5a616b);
  background: -o-linear-gradient(right, #b695a6, #725d6c, #3d3f4b, #5a616b);
  background: linear-gradient(-90deg, #b695a6, #725d6c, #3d3f4b, #5a616b);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
  animation: Gradient 15s ease infinite;
}



