/*

CSS GOES HERE

*/

.border {
  border-bottom: 1px solid #ccc;
  margin-bottom: 2rem;
}

.images {
  float: left;
}

.title {
  display: -ms-flexbox;
  display: flex;

}

p {
  text-transform: none;
}

.padding {
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.title {
  padding-top: 0rem;
  padding-left: 1rem;
  padding-bottom: 1rem;
  margin-bottom: 0.5rem;
}

.card {
  background-color: #ffffff;
  padding: 2rem;
  margin: auto;
  max-width: 1080px;
  /*max-height: 720px;*/
  min-height: 24rem;
  -webkit-box-shadow: 0px 0px 50px rgba(0, 0, 0, .7);
          box-shadow: 0px 0px 50px rgba(0, 0, 0, .7);
  /*padding: 100px;*/
}


ul {
    list-style: none;
    padding: 1rem;
}

.navButton {
  font-size: 21px;
  display: inline-block;
  padding: 2rem;
  color: #505050;
}

.navButton a {
  text-decoration: none;
  color: #ffffff;
}

a {
	text-decoration: none;
}

.navButton a:hover {
  color: #F84443;
}

section ul {
	text-align: center;
}

section a {
	padding: 7px;
  color: #ffffff;
}

section a:not(:first-child) {
	margin-left: 20px;
}

section i {
	vertical-align: middle;
	-webkit-transition: color .3s ease-in-out;
	-o-transition: color .3s ease-in-out;
	transition: color .3s ease-in-out;
}

section a:nth-child(1):hover i {
	color: #F84443;
}

section a:nth-child(2):hover i {
	color: #F84443;
}

section a:nth-child(3):hover i {
	color: #F84443;
}

/* Main Button Style  */

.btn {
  color: #505050;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 2rem;
  padding-right: 2rem;
  border-radius: 1px;
  background: none;
  border: 1px solid #505050;
  font-size: 12px;
}

.btn:hover {
  border: none;
  -webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
          box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
  color: #FFFFFF;
  background-color: #F84443;
}

.resume {
  padding-top: 5rem;
}

/* End Main Button Style */

p {
  font-size: 16px;
}

/* Responsive */



.flex-grid {
  display: -ms-flexbox;
  display: flex;
}
.flex-grid .col {
  -ms-flex: 1 1;
      flex: 1 1;
}

@media (max-width: 1080px) {
  .flex-grid {
    display: block;
    .col {
      width: 100%;
      margin: 0 0 10px 0;
    }
  }
}

@media (max-width: 9000px) {
  .Mobile{
    display: none;
  }
}

@media (max-width: 480px) {
  .flex-grid {
    display: block;
    .col {
      width: 100%;
      margin: 0 0 10px 0;
    }
    .p2 {
      margin: inherit;
    }
  }
  .card {
    padding: 0;
  }

  .resume {
    display: none;
  }

  .Mobile {
    display: inline;
  }

  .nonMobile {
    display: none;
  }

  .title {
    padding: inherit;
    padding-top: 1rem;
  }

}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  padding: 20px;
}
.flex-grid {
  margin: 0 0 20px 0;
}
.col {
  padding: 20px;
}

.footerBake {
  color: #ffffff;
  text-transform: uppercase;
}

.footerBake:hover {
  color: #F84443;
}


.parent {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.right {
       -ms-flex-positive: 1;
           flex-grow: 1;
   }

